@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
*{padding:0;margin:0;border:0}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:inherit}

.App{text-align:center;background-color:#f5f8fa;min-height:100vh;font-family:"Ubuntu", sans-serif;padding:20px 10px 20px 10px}

.title{color:#1ea0f2;margin:auto;text-align:center;display:flex;flex-direction:column;align-items:center}.title__first{font-size:35px;font-weight:900;margin:20px 10px}@media screen and (max-width: 560px){.title__first{font-size:30px;margin:15px 10px}}@media screen and (max-width: 460px){.title__first{font-size:22px;margin:15px 10px 10px 10px}}.title__second{font-size:26px;font-style:italic;display:flex;align-items:center}.title__second p{margin:5px 10px 5px 5px}@media screen and (max-width: 560px){.title__second{font-size:24px}.title__second p{margin:5px 8px 5px 5px}}@media screen and (max-width: 460px){.title__second{font-size:20px}.title__second p{margin:5px 8px 5px 5px}}.form-name{margin:20px 0 10px 0}.form-name__field{width:800px;color:#657786;font-size:20px;text-align:left;border-radius:4px;border:1px solid #1ea0f2;background-color:#fff;padding:8px 15px}@media screen and (max-width: 860px){.form-name__field{width:700px}}@media screen and (max-width: 760px){.form-name__field{width:600px}}@media screen and (max-width: 660px){.form-name__field{width:500px}}@media screen and (max-width: 560px){.form-name__field{width:400px;font-size:16px}}@media screen and (max-width: 460px){.form-name__field{width:300px}}.form-text{margin:15px 0 5px 0}.form-text__field{resize:none;width:800px;color:#657786;font-size:20px;text-align:left;border-radius:4px;border:1px solid #1ea0f2;background-color:#fff;padding:8px 15px}@media screen and (max-width: 860px){.form-text__field{width:700px}}@media screen and (max-width: 760px){.form-text__field{width:600px}}@media screen and (max-width: 660px){.form-text__field{width:500px}}@media screen and (max-width: 560px){.form-text__field{width:400px;font-size:16px}}@media screen and (max-width: 460px){.form-text__field{width:300px}}.form-submit-btn{margin:8px 0 30px 0;background-color:#1ea0f2}.form-submit-btn:disabled{background-color:#ccc;cursor:not-allowed}.form-submit-btn__text-wrapper{width:800px;display:flex;justify-content:center;align-items