.comment-wrapper{width:800px;margin:auto;min-height:120px}@media screen and (max-width: 860px){.comment-wrapper{width:700px}}@media screen and (max-width: 760px){.comment-wrapper{width:600px}}@media screen and (max-width: 660px){.comment-wrapper{width:500px}}@media screen and (max-width: 560px){.comment-wrapper{width:400px}}@media screen and (max-width: 460px){.comment-wrapper{width:300px}}.comment{min-height:120px;margin:10px 0 8px 0;display:flex;justify-content:space-between;align-items:center;border-radius:6px;background-color:#d5ebf8}.comment-user{width:25%;min-height:120px;display:flex;flex-direction:column;justify-content:space-between;align-items:center;background-color:#74c4f7;border-bottom-left-radius:4px;border-top-left-radius:4px}@media screen and (max-width: 560px){.comment-user{width:40%}}.comment-user__photo{margin:10px 10px}@media screen and (max-width: 560px){.comment-user__photo{margin:5px 5px}}.comment-user__icon{font-size:46px;color:#fff;margin:2px}@media screen and (max-width: 560px){.comment-user__icon{font-size:30px}}.comment-user__name{display:flex;flex-direction:column;justify-content:space-between;align-items:center;margin-bottom:12px;color:#000;font-size:22px}.comment-user__name p{margin-bottom:5px;font-size:14px;font-style:italic;color:#fff}@media screen and (max-width: 660px){.comment-user__name{font-size:18px}}@media screen and (max-width: 560px){.comment-user__name{font-size:16px;margin-bottom:8px}}@media screen and (max-width: 460px){.comment-user__name{font-size:14px}}.comment-user__time{font-size:14px;font-style:italic;color:#fff;margin-bottom:5px;display:flex;align-items:center}.comment-user__time span{margin:5px 3px 5px 8px;font-size:16px}@media screen and (max-width: 760px){.comment-user__time{flex-direction:column}}@media screen and (max-width: 560px){.comment-user__time{font-size:12px;margin-bottom:3px}.comment-user__time span{margin:3px 3px 3px 5px;font-size:16px}}.comment-user__time1{display:flex;align-items:center}.comment-user__time2{display:flex;align-items:center}.comment-text{background-color:#d5ebf8;width:75%;height:120px;padding:10px 30px;text-align:left}@media screen and (max-width: 560px){.comment-text{padding:5px 10px}}@media screen and (max-width: 460px){.comment-text{padding:2px 10px}}.comment-text__message{min-height:100%;font-size:22px}@media screen and (max-width: 660px){.comment-text__message{font-size:18px}}@media screen and (max-width: 560px){.comment-text__message{font-size:16px}}@media screen and (max-width: 460px){.comment-text__message{font-size:14px}}

.more-btn-wrapper{margin:auto;text-align:center;display:flex;flex-direction:column;align-items:center}.more-btn{margin:20px 0;width:800px;display:flex;justify-content:center;align-items:center;border-radius:4px;border:1px solid #1ea0f2;background-color:#1ea0f2}@media screen and (max-width: 860px){.more-btn{width:700px}}@media screen and (max-width: 760px){.more-btn{width:600px}}@media screen and (max-width: 660px){.more-btn{width:500px}}@media screen and (max-width: 560px){.more-btn{width:400px}}@media screen and (max-width: 460px){.more-btn{width:300px}}.more-btn:hover{border:1px solid #1585ca}.more-btn-inner-wrapper{display:flex;justify-self:center;align-items:center}.more-btn__text{color:#fff;font-size:20px;text-align:left;background-color:#1ea0f2;padding:8px 10px}.more-btn__icon{color:#fff;font-size:26px}

.pagination-wrapper{margin:auto;text-align:center;display:flex;flex-direction:column;align-items:center;margin-top:10px}.pagination-container{width:800px;display:flex;justify-content:space-between;margin:30px 0}@media screen and (max-width: 860px){.pagination-container{width:700px}}@media screen and (max-width: 760px){.pagination-container{width:600px}}@media screen and (max-width: 660px){.pagination-container{width:500px}}@media screen and (max-width: 560px){.pagination-container{width:400px;margin:20px 0}}@media screen and (max-width: 460px){.pagination-container{width:300px}}.pageLinkClassName{padding:10px 20px;background-color:#d5ebf8;border-radius:4px;cursor:pointer}@media screen and (max-width: 560px){.pageLinkClassName{width:400px;padding:10px}}@media screen and (max-width: 460px){.pageLinkClassName{width:300px;padding:10px 6px}}.activeLinkClassName{background-color:#4ba5da;cursor:auto}.previousLinkClassName{padding:10px 20px;border-radius:4px;background-color:#88a2bb;cursor:pointer}@media screen and (max-width: 560px){.previousLinkClassName{width:400px;padding:10px}}@media screen and (max-width: 460px){.previousLinkClassName{width:300px;padding:10px 6px}}.nextLinkClassName{padding:10px 20px;border-radius:4px;background-color:#88a2bb;cursor:pointer}@media screen and (max-width: 560px){.nextLinkClassName{width:400px;padding:10px}}@media screen and (max-width: 460px){.nextLinkClassName{width:300px;padding:10px 6px}}.disabledClassName a{background-color:#8b8b8b;cursor:not-allowed;opacity:0.3}.breakLinkClassName{cursor:pointer}

